 * @LastEditTime: 2020-02-26 10:13:23
 * @Description: 数据采集-采集任务管理-转换规则配置
 -->
 
<template>
  <div class="r-c">
    <div class="r-c-edit-area">
      <ul class="r-c-edit-area-btn">
        <li>
          <el-button type="primary" size="medium" @click="showTableDialog({type:'添加目标表'})">添加目标表</el-button>
        </li>
        <li>
          <el-tooltip class="item" effect="dark" content="比较两个或多个项目之间的元数据的差异" placement="top">
            <el-button type="primary" size="medium">批量效验</el-button>
          </el-tooltip>
        </li>
      </ul>
      <ul class="r-c-edit-area-select">
        <li>
          <label for>任务：</label>
          <el-select
            v-model="selectVal"
            filterable
            placeholder="请选择"
            size="small"
            @change="selectChange"
          >
            <el-option
              v-for="item in selectOpt"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </li>
      </ul>
    </div>
    <div class="r-c-table">
      <TableDefault
        :TTitle="tableTitle"
        :TData="tableData"
        :TIsShowPage="true"
        :TTotal="TPage.total"
        :TCurrentPage="TPage.index"
        :TPageSize="TPage.size"
        :TEdit="TEdit"
        @editBtnCb="editBtnCb"
        @clickCell="lickCell"
        @changePage="changePage"
      ></TableDefault>
    </div>
    <!-- 弹出框 -->
    <TableDialog
      :TableDigTitle="'添加目标表'"
      :TableDigVisible="TableDigVisible"
      :TableDigLoading="TableDigLoading"
      :TableDigWidth="800"
      :TableData="TableData"
      :TableTitle="TableTitle"
      :TabledigHeight="452"
      :TableDigShowCheckBox="true"
      :TableDigEdit="TableDigEdit"
      :TableDigPage="TableDigPage"
      :TableDigFoot="TableDigFoot"
      @editBtnCb="editBtnCb"
      @clickCell="lickCell"
    ></TableDialog>
    <TableDialog
      :TableDigTitle="'字段映射配置'"
      :TableDigVisible="TableDigVisible_1"
      :TableDigLoading="TableDigLoading"
      :TableDigWidth="800"
      :TableData="TableData"
      :TableTitle="TableTitle_1"
      :TabledigHeight="452"
      :TableDigIndex="false"
      :TableDigEdit="TableDigEdit_1"
      :TableDigPage="TableDigPage"
      :TableDigFoot="TableDigFoot"
      @editBtnCb="editBtnCb"
      @clickCell="lickCell"
    ></TableDialog>
  </div>
</template>

<script>
import mixin from "mixin";
import TableDialog from "@/components/dialog/table-dialog";
export default {
  mixins: [mixin],
  components: {
    TableDialog
  },
  data() {
    return {
      // 分页
      TPage: {
        index: 1,
        size: 10,
        total: 0
      },
      // 编辑-表格配置
      TEdit: {
        show: true,
        title: "操作",
        editTypes: [
          {
            label: "删除"
          },
          {
            label: "编辑源数据集"
          },
          {
            label: "字段映射配置"
          }
        ]
      },
      tableTitle: [
        {
          prop: "name",
          label: "所属项目"
        },
        {
          prop: "date",
          label: "数据表个数"
        },
        {
          prop: "address",
          label: "字段个数"
        }
      ],
      tableData: [
        {
          date: "122",
          name: "通用",
          address: "45"
        },
        {
          date: "122",
          name: "海淀",
          address: "45"
        },
        {
          date: "122",
          name: "山西",
          address: "45"
        },
        {
          date: "122",
          name: "济南",
          address: "45"
        },
        {
          date: "122",
          name: "山东",
          address: "45"
        },
        {
          date: "122",
          name: "贵州",
          address: "45"
        },
        {
          date: "122",
          name: "福州",
          address: "45"
        },
        {
          date: "122",
          name: "泰安",
          address: "45"
        },
        {
          date: "122",
          name: "南京",
          address: "45"
        },
        {
          date: "122",
          name: "广州",
          address: "45"
        }
      ],
      // 是否显示弹出框
      FDVisible: false,
      FDTitle: "",
      // 弹出框配置
      FDForm: [
        {
          type: "select",
          label: {
            name: "所属项目",
            width: "120"
          },
          width: "200",
          defaultModel: "",
          placeholder: "",
          option: [
            {
              label: "海淀",
              value: "haidian"
            }
          ]
        },
        {
          type: "input",
          label: {
            name: "中文名称",
            width: "120"
          },
          width: "280",
          defaultModel: "",
          placeholder: ""
        },
        {
          type: "input",
          label: {
            name: "库名",
            width: "120"
          },
          width: "280",
          defaultModel: "",
          placeholder: ""
        },
        {
          type: "textarea",
          label: {
            name: "备注",
            width: "120"
          },
          width: "280",
          defaultModel: "",
          placeholder: "",
          autosize: {
            min: 2,
            max: 4
          }
        }
      ],
      FDFoot: {
        button: [
          {
            type: "ok",
            label: "确定",
            cb: form => {
              console.log("新增任务请求");
              switch (this.dialogActiveItem) {
                case "新增任务":
                  // TODO
                  console.log("新增任务请求");
                  return;
                case "编辑":
                  console.log("编辑请求");
                  return;

                default:
                  break;
              }
            }
          }
        ]
      },
      // 下拉选择
      selectOpt: [
        {
          value: "选项1",
          label: "所有项目"
        },
        {
          value: "选项2",
          label: "通用"
        },
        {
          value: "选项3",
          label: "南京"
        },
        {
          value: "选项4",
          label: "山东"
        },
        {
          value: "选项5",
          label: "济南"
        }
      ],
      selectVal: "选项1",
      // 激活的弹出框对象
      dialogActiveItem: "",
      // 表格弹出配置
      TableDigVisible: false,
      TableDigVisible_1: false,
      TableData: [
        {
          date: "2016-05-02",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1518 弄"
        },
        {
          date: "2016-05-04",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1517 弄"
        },
        {
          date: "2016-05-01",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1519 弄"
        },
        {
          date: "2016-05-03",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1516 弄"
        },
        {
          date: "2016-05-02",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1518 弄"
        },
        {
          date: "2016-05-04",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1517 弄"
        },
        {
          date: "2016-05-01",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1519 弄"
        },
        {
          date: "2016-05-03",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1516 弄"
        },
        {
          date: "2016-05-02",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1518 弄"
        },
        {
          date: "2016-05-04",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1517 弄"
        },
        {
          date: "2016-05-01",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1519 弄"
        },
        {
          date: "2016-05-03",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1516 弄"
        }
      ],
      TableTitle: [
        {
          label: "目标表",
          prop: "date"
        },
        {
          label: "源表",
          prop: "name"
        }
      ],
      TableTitle_1: [
        {
          label: "目标表",
          prop: "date"
        },
        {
          label: "源类型",
          prop: "name",
          type: "select"
        },
        {
          label: "源配置值",
          prop: "date"
        },
        {
          label: "目标字段中文名",
          prop: "name"
        }
      ],
      TableDigTitle: "",
      TableDigWidth: 800,
      TableDigLoading: true,
      TabledigHeight: 0,
      TableDigShowCheckBox: true,
      TableDigEdit: {
        show: false,
        label: "操作",
        fixed: "right",
        types: [
          {
            label: "删除"
          }
        ]
      },
      TableDigEdit_1: {
        show: true,
        label: "操作",
        fixed: "right",
        types: [
          {
            label: "删除"
          }
        ]
      },
      TableDigFoot: {
        show: true,
        data: [
          {
            type: "button",
            classType: "primary",
            class: "",
            label: "确定",
            cb: obj => {
              console.log(obj);
            }
          }
        ]
      },
      TableDigIndex: true,
      TableDigPage: {
        show: true,
        currentPage: 1,
        pageSize: 10,
        total: 0
      }
    };
  },
  methods: {
    editBtnCb(editItem) {
      console.log(editItem);
      let text = editItem.label;
      if (text === "删除") {
        // TODO
        console.log("删除请求");
      } else if (text === "close") {
        this.TableDigVisible = editItem.close;
        this.TableDigVisible_1 = editItem.close;
      }
    },
    // 分页操作
    changePage({ pageSize, currentPage }) {
      this.$set(this.TPage, `index`, currentPage);
      this.$set(this.TPage, `size`, pageSize);
      // this.DSMCList();
      // TODO
    },
    // 表格弹出框
    showTableDialog({ type }) {
      this.dialogActiveItem = type;
      switch (type) {
        case "添加目标表":
          this.TableDigLoading = true;
          this.TableDigVisible = true;
          // TODO 获取数据并赋值
          setTimeout(() => {
            // this.TableData = [];
            this.TableDigLoading = false;
          }, 2000);
          return;
        case "字段映射配置":
          this.TableDigLoading = true;
          this.TableDigVisible_1 = true;
          this.TabledigHeight = 452;
          // TODO 获取数据并赋值
          setTimeout(() => {
            // this.TableData = [];
            this.TableDigLoading = false;
          }, 2000);
          // this.TableDigLoading = true;
          // this.TableDigVisible = true;
          // this.TabledigHeight = 452;
          // this.TableDigShowCheckBox = false;
          // this.TableTitle = [
          //   {
          //     label: "目标表字段",
          //     prop: "date"
          //   },
          //   {
          //     label: "源类型",
          //     prop: "name",
          //     type: "select"
          //   },
          //   {
          //     label: "源配置值",
          //     prop: "date"
          //   },
          //   {
          //     label: "目标字段中文名",
          //     prop: "name"
          //   }
          // ];
          // this.TableDigEdit = {
          //   show: true,
          //   label: "操作",
          //   fixed: "right",
          //   types: [
          //     {
          //       label: "清空",
          //       pass: true
          //     }
          //   ]
          // };
          // // this.TableData = [];
          // // TODO 获取数据并赋值
          // setTimeout(() => {
          //   this.TableDigLoading = false;
          // }, 1000);
          return;
        default:
          break;
      }
    },
    // 选择下拉
    selectChange(item) {
      // TODO 发请求
      console.log(item);
    },
    // 单元格
    lickCell({ row, column, id }) {
      switch (column.label) {
        case "清空":
          console.log("表格弹出框删除按钮", row);
          return;
        case "编辑源数据集":
          console.log("弹出框");
          return;
        case "字段映射配置":
          this.showTableDialog({ type: column.label });
          return;
        default:
          break;
      }
    }
  },
  mounted() {}
};
</script>

<style lang="less" scoped>
.r-c {
  &-edit-area {
    overflow: hidden;
    &-btn {
      float: left;
      overflow: hidden;
      li {
        float: left;
        margin: 2px 15px;
      }
    }
    &-select {
      float: left;
      overflow: hidden;
      margin: 4px 55px;
    }
  }
  &-table {
    margin: 20px 0;
  }
}
</style>



